import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './Index.scss';

/*  eslint-disable react/prefer-stateless-function */
// DateSetterComponent makes use of ref with this component

class Input extends Component {
  static propTypes = {
    isError: PropTypes.bool,
    className: PropTypes.string,
  }

  static defaultProps = {
    isError: false,
    className: '',
  }

  render() {
    const { isError, className, ...otherProps } = this.props;

    return (
      <input
        type="text"
        className={`input ${isError ? 'input--error' : ''} ${className}`}
        {...otherProps}
      />
    );
  }
}

export default Input;
