import { UPDATE_FREE_USERS } from '../../consts/dropdownReducer';

const updateFreeUsers = (users, addedUsers, currentTime, editing) => {
  const notAddedUsers = users.filter(user => !addedUsers.some(addedUser => (
    user.id === addedUser.id
  )));

  const freeUsers = notAddedUsers.filter((user) => {
    if (user.busyTime.length > 0) {
      const isUserFree = ({ startingHours, startingMinutes, endingHours, endingMinutes }) => (
        (endingHours <= currentTime.startingHours
        && endingMinutes <= currentTime.startingMinutes)
        || (startingHours >= currentTime.endingHours
        && startingMinutes >= currentTime.endingMinutes)
      );

      return user.busyTime.every((time) => {
        if (editing) {
          return true; // It should let user re-select selected participators while editing
        }

        return isUserFree(time);
      });
    }

    return true; // User is not added in any event
  });

  return {
    type: UPDATE_FREE_USERS,
    payload: freeUsers,
  };
};

export default updateFreeUsers;
